<template>
  <div class="form-bg">
    <el-form :model="forminfo" :rules="rules" ref="form" label-width="80px">
      <el-tabs v-model="activeName">
        <!-- 基本信息 -->
        <el-tab-pane label="基本信息" name="first">
          <el-col :span="10" :offset="6">
            <el-form-item label="一级分类" prop="first_cateid">
              <el-select @change="topChange" v-model="forminfo.first_cateid" placeholder="请选择">
                <el-option
                  v-for="item in catelist"
                  :key="item.id"
                  :label="item.catename"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="二级分类" prop="second_cateid">
              <el-select v-model="forminfo.second_cateid" placeholder="请选择">
                <el-option
                  v-for="item in second_list"
                  :key="item.id"
                  :label="item.catename"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="分类名称" prop="goodsname">
              <el-input v-model="forminfo.goodsname"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="price">
              <el-input v-model="forminfo.price"></el-input>
            </el-form-item>
            <el-form-item label="市场价格" prop="market_price">
              <el-input v-model="forminfo.market_price"></el-input>
            </el-form-item>
            <el-form-item label="分类图片">
              <el-upload
                action="#"
                :auto-upload="false"
                list-type="picture-card"
                :limit="1"
                :file-list="fileList"
                :on-change="handleChange"
                :on-preview="handlePictureCardPreview"
                :on-remove="handleRemove"
              >
                <i class="el-icon-plus"></i>
              </el-upload>
              <el-dialog :visible.sync="dialogVisible">
                <img width="100%" :src="dialogImageUrl" alt />
              </el-dialog>
            </el-form-item>

            <el-form-item label="商品属性" prop="specsid">
              <el-select v-model="forminfo.specsid" @change="specsChange" placeholder="请选择">
                <el-option
                  v-for="item in specslist"
                  :key="item.id"
                  :label="item.specsname"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="属性值" prop="specsattr">
              <el-select multiple v-model="forminfo.specsattr" placeholder="请选择">
                <el-option
                  v-for="item in attr_val_list"
                  :key="item"
                  :label="item"
                  :value="item"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="是否新品" prop="isnew">
              <el-switch v-model="forminfo.isnew" :active-value="1" :inactive-value="2"></el-switch>
            </el-form-item>
            <el-form-item label="是否热卖" prop="ishot">
              <el-switch v-model="forminfo.ishot" :active-value="1" :inactive-value="2"></el-switch>
            </el-form-item>
            <el-form-item label="状态">
              <el-switch v-model="forminfo.status" :active-value="1" :inactive-value="2"></el-switch>
            </el-form-item>
          </el-col>
        </el-tab-pane>
        <!-- 详细信息 -->
        <el-tab-pane label="详细描述" name="second">
          <vue-wangeditor  :disabledMenus="['location']" ref="editor" width="100%" id="editor" v-model="forminfo.description"></vue-wangeditor>
          <br>
          <br>
          <br>
        </el-tab-pane>
      </el-tabs>
      <el-form-item>
        <el-button type="primary" @click="submit">立即{{$route.params.id ? '修改':'创建'}}</el-button>
        <el-button @click="reset">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import vueWangeditor from 'vue-wangeditor' // 编辑器插件！
import { mapGetters, mapActions } from "vuex";
import { addGoods,editGoods, getGoodsInfo } from "@/request/goods"
export default {
  data() {
    return {
      activeName: "first",
      second_list: [], // 二级分类
      attr_val_list: [], // 属性列表
      forminfo: {
        first_cateid: "",
        second_cateid: "",
        goodsname: "测试手机",
        price: "200",
        market_price: "300",
        description: "很好的手机啊",
        specsid: "",
        specsattr: [],
        isnew: 2,
        ishot: 2,
        img: "",
        status: 1, // 状态，1是正常，2是禁用
      },
      dialogImageUrl: "",
      dialogVisible: false,
      fileList: [], // [{name:"",url:""}]
      rules: {
        first_cateid: [{ required: true, message: "请填写", trigger: "blur" }],
        second_cateid: [{ required: true, message: "请填写", trigger: "blur" }],
        goodsname: [{ required: true, message: "请填写", trigger: "blur" }],
        price: [{ required: true, message: "请填写", trigger: "blur" }],
        market_price: [{ required: true, message: "请填写", trigger: "blur" }],
        description: [{ required: true, message: "请填写", trigger: "blur" }],
        specsid: [{ required: true, message: "请填写", trigger: "blur" }],
        specsattr: [{ required: true, message: "请填写", trigger: "blur" }],
        isnew: [{ required: true, message: "请填写", trigger: "blur" }],
        ishot: [{ required: true, message: "请填写", trigger: "blur" }],
      },
    };
  },
  computed: {
    ...mapGetters({
      catelist: "category/catelist",
      specslist: "specs/specslist",
    }),
  },
   mounted() {
    if (!this.catelist.length) {
       this.getCateList();
    }
    if(!this.specslist.length){
       this.getSpecsList();
    }
    if(this.$route.params.id){
      this.getInfo();
    }
  },
  methods: {
    ...mapActions({
      getCateList: "category/getlist",
      getSpecsList: "specs/getlist",
    }),
    topChange(vid) {
      this.second_list = []
      this.catelist.forEach( val=> {
          if(val.id==vid && val.children){
              this.second_list = val.children
          }
      });
    },
    specsChange(vid){
      this.attr_val_list = [];
      this.forminfo.specsattr = [];
      this.specslist.forEach( val=> {
          if(val.id==vid && val.attrs){
              this.attr_val_list = val.attrs
          }
      });
    },
    handleChange(file, fileList) {
      // 将原生的文件信息放入到img字段中
      this.forminfo.img = file.raw;
    },
    handleRemove(file, fileList) {
      this.fileList = [];
      this.forminfo.img = "";
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    submit() {
      // 验证
      // 如果文件信息和字段信息一起提交，那么需要使用FormData对象！
      console.log(this.forminfo);
      console.log( this.$refs.editor.getHtml())
      this.forminfo.description = this.$refs.editor.getHtml();
      let fd = new FormData();
      for (let key in this.forminfo) {
        fd.append(key, this.forminfo[key]);
      }
      this.$refs.form.validate((valid) => {
        if (valid) {
          // 提交数据
          if (this.$route.params.id) {
            editGoods(this.$route.params.id,fd);
          } else {
            addGoods(fd);
          }
        } else {
          this.$message.warning("验证失败");
        }
      });
    },
    async getInfo(){
        let res = await getGoodsInfo(this.$route.params.id);
        // 处理图片回显
        if(res.img){
          this.fileList = [{
            name:res.goodsname,
            url:this.$host+res.img
          }]
        }
        this.forminfo = res;
        // 处理二级分类回显
        this.topChange(this.forminfo.first_cateid);
        // 处理属性的回显
        this.forminfo.specsattr = this.forminfo.specsattr.split(",")
        this.specslist.forEach( val=> {
            if(val.id==this.forminfo.specsid && val.attrs){
                this.attr_val_list = val.attrs
            }
        });

    },
    reset() {
      if (this.$route.params.id) {
        this.getinfo();
      } else {
        this.$refs.form.resetFields();
      }
    },
  },
  components: {
    vueWangeditor
  }
};
</script>
<style scoped>
</style>


